.spoiler {
	color: transparent;
	text-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
	transition: all .4s;
	cursor: pointer;
	position: relative;

	&:after {
		position: absolute;
		opacity: 0;
		content: 'Click to reveal spoiler';
		top: 45%;
		left: calc(50% - 75px);
		text-shadow: none;
		background: #222;
		color: #fff;
		display: inline-block;
		font-size: 13px;
		line-height: 1;
		padding: 2px 3px;
		width: 150px;
		font-family: Arial;
		text-align: center;
		border-radius: 3px;
		transition: all .4s;
	}

	&:hover:after {
		opacity: 1;
	}
}
